import React, {useEffect, useState} from 'react';
import {Link, useNavigate, useParams } from 'react-router-dom';
import { Row,Col,ListGroup,Image,Card, Button} from 'react-bootstrap';
import {useDispatch, useSelector} from 'react-redux';
import { deliverOrder, getOrderDetails, payOrder } from '../actions/orderActions';

import { PayPalButton } from 'react-paypal-button-v2'

//import {saveShippingAddress} from '../actions/cartActions'
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET } from '../constant/orderConstants'

import Message from '../components/Message';
import Loader from '../components/Loader';
//import {useNavigate} from 'react-router-dom';


const OrderScreen = () => {
    let params = useParams();
    const orderId = params.id;
    const history = useNavigate()
    
    const [sdkReady, setSdkReady] = useState(false);
    console.log(orderId)
    const orderDetails = useSelector(state => state.orderDetails);
    const {order,error, loading } = orderDetails

    const orderPay = useSelector(state => state.orderPay);
    const {loading:loadingPay,success: successPay } = orderPay

    const orderDeliver = useSelector(state => state.orderDeliver);
    const {loading:loadingDeliver,success: successDeliver } = orderDeliver

    const userLogin = useSelector(state => state.userLogin)

    const { userInfo } = userLogin

    const dispatch = useDispatch()
    
    //const history = useNavigate();
    //console.log(cart)
    

    console.log(order);
    console.log(loading)
    //dispatch(getOrderDetails(orderId));
    const addPaypalScript = () =>{
        const script = document.createElement('script');
        script.type = 'text/javascript'
        script.src = 'https://www.paypal.com/sdk/js?client-id=test'
        script.async = true
        script.onload = () =>{
            setSdkReady(true)
        }
        document.body.appendChild(script);
    }

    if(!loading && !error){
        order.itemsPrice = order.orderItems.reduce((acc,item) => acc + item.price * item.qty,0).toFixed(2);
    }
        
    // if(!order || order._id === Number(orderId)){
    //         dispatch(getOrderDetails(orderId))
    //     }
    useEffect(()=>{
        if(!userInfo){
            history('/login')
        }
        if(!order || successPay|| order._id === orderId || successDeliver){
            dispatch({type: ORDER_PAY_RESET})
            dispatch({type: ORDER_DELIVER_RESET})
            dispatch(getOrderDetails(orderId))
        }else if(!order.isPaid){
            if(!window.paypal){
                addPaypalScript();
            }else{
                setSdkReady(true)
            }
        }
       
    },[dispatch,order,orderId, successPay, successDeliver,history,userInfo])

    
    const successPaymentHandler = (paymentResult) =>{
        dispatch(payOrder(orderId,paymentResult))
    }

    const deliverHandler = () =>{
        dispatch(deliverOrder(order))
    }

    
    return (loading ? (<Loader /> ): error ? (<Message variant='danger'>{error}</Message>) : (
    // return(
        <div>
            <Row>
                <h1>Order: {orderId}</h1>
                <Col md={8}>
                    <ListGroup variant="flush">
                        <ListGroup.Item>
                            <h2>Shipping</h2>
                            <p><strong>Name: </strong>{order.user.name}</p>
                            <p><strong>Email: </strong><a href={`mailto:${order.user.email}`}>{order.user.email}</a></p>
                            <p>
                                <strong>Shipping: </strong>
                                {order.shippingAddress.address},
                                {order.shippingAddress.city},
                                {order.shippingAddress.postalCode},
                                {order.shippingAddress.country},
                            </p>
                            {order.isDelivered ? (<Message variant="success">Delivered on {order.deliveredAt}</Message>) : (<Message variant="warning">Not Delivered</Message>)}
                            
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <h2>Payment Method</h2>
                            <p>
                                <strong>Method: </strong>
                                {order.paymentMethod}
                                
                            </p>
                            {order.idPaid ? (<Message variant="success">Paid on {order.paidAt}</Message>) : (<Message variant="warning">Not Paid</Message>)}
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <h2>Order Items</h2>
                            {order.orderItems.length ===0 ? <Message variant='info'>Your order is empty</Message> : (
                                <ListGroup variant="flush">
                                    {
                                        order.orderItems.map((item,index) =>(
                                            <ListGroup.Item key={index}>
                                                <Row>
                                                    <Col md={1}>
                                                    <Image src={item.image} alt={item.name} fluid rounded/>
                                                    </Col>
                                                    <Col >
                                                    <Link to={`/product/${item.product}`}>{item.name}</Link>
                                                    </Col>
                                                    <Col md={4}>
                                                    {item.qty} X ${item.price} = ${(item.qty * item.price).toFixed(2)}
                                                    </Col>
                                                </Row>
                                            </ListGroup.Item>
                                        ))
                                    }
                                </ListGroup>
                            )}
                        </ListGroup.Item>
                    </ListGroup>
                    
                </Col>

                <Col md={4}>
                    <Card>
                        <ListGroup variant="flush">
                            <ListGroup.Item>
                                <h2>Order Summary: </h2>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Item: </Col>
                                    <Col>${order.itemsPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Shipping: </Col>
                                    <Col>${order.shippingPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Tax: </Col>
                                    <Col>${order.taxPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Total Price: </Col>
                                    <Col>${order.totalPrice}</Col>
                                </Row>
                            </ListGroup.Item>

                            {!order.idPaid && (<ListGroup.Item>
                                {loadingPay && <Loader />}

                                {!sdkReady ? (<Loader />) : (
                                    <PayPalButton 
                                        amount={order.totalPrice}
                                        onSuccess={successPaymentHandler}
                                    />
                                )}
                            </ListGroup.Item>)} 





                        </ListGroup>
                        {loadingDeliver && <Loader />}
                        {userInfo && userInfo.isAdmin && order.idPaid && !order.isDelivered && (
                            <ListGroup.Item>
                                <Button
                                    type="button"
                                    className="btn btn-block"
                                    onClick={deliverHandler}
                                >
                                    Mark as Deliver
                                </Button>
                            </ListGroup.Item>
                        )}
                    </Card>
                </Col>
            </Row>
        </div>
    ));
};

export default OrderScreen;